@mixin flex-container($justify-content, $direction) {
  display: flex;
  align-items: center;
  justify-content: $justify-content;
  flex-direction: $direction; }


.Panel__in {
  background-color: blue; }

.card {
  //display: flex
  //align-items: start
  //justify-content: start
  //flex-direction: column
  box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
  transition: 0.3s;
  margin: 10px 2px 10px 2px;
  background-color: white;
  border-radius: 10px;
  padding: 5px; }


.card:hover {
  box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2); }

.bg-color {
  background-color: white; }

.myItem {
  word-wrap: break-word;
  white-space: pre-wrap; }


.button {
  @include flex-container(start, column);
  margin-top: 30px;
  padding: 0;
  position: fixed;
  top: 340px;
  width: 100%; }


.list {
  @include flex-container(center, column);
  //margin: 0 0 0 0
  //padding: 0
  word-wrap: break-word; }

.Cell__main {
  white-space: pre-wrap; }

.Cell__children {
  color: black;
  font-size: 80%; }

.Cell--ios .Cell__in {
  padding: 0; }

.favorites {
  @include flex-container(center, column);
  margin: 30px; }

.divButton {
  padding: 0;
  margin: 10px; }

.divSpinner {
  @include flex-container(center, column);
  padding: 0;
  margin: 20% auto 0; }

.buttonDisabled {
  color: white;
  background-color: gray; }

.buttonAddGroup {
  padding: 0;
  margin: 10px 0 10px 0; }

.buttonStory {
  padding: 0;
  margin: 10px 0 10px 0; }

